

.Logo-section {
    background-color: #0d0f1b;
    align-items: center;
    display: flex;
    padding: 90px 0;
}
.Logo-images{
    justify-content: center;
    display: flex;
}
.Logo-images img{
    width: 165px;
    height: auto;
    cursor: pointer;
}

/* RESPONSIVENESS START */

@media screen and (max-width: 992px) {
    .Logo-section {
        padding: 50px 0;
    }
}